import uniLoadmore from "../../../components/uni-load-more/uni-load-more";
export default {
	data() {
		return {
			type: 1,
			nowIndex: 1,
			bar: ["信用卡", "储蓄卡"],
			cx_card: '',
			xy_card: '',
			icon: true,
			style:'background: #9d9d9d;'
		};
	},
	components: {
		uniLoadmore
	},
	computed: {},
	methods: {
		xiugai(id) {
			uni.navigateTo({
				url: 'edit-credit-card?id=' + id
			})
		},
		close(id){
			uni.showModal({
			    title: '提示',
			    content: '是否确认解绑该信用卡？',
			    success: function (res) {
			        if (res.confirm) {
			            that.$store.dispatch('deleteCard',{
			            	creditid:id
			            }).then(res=>{
			            	uni.showToast({
			            		title: res.msg,
			            		icon: 'none', //loading   none  success
			            		success: (res)=>{
			            			setTimeout(function(){
			            				that.getData()
			            			},1500)
			            		}
			            	});
			            }).catch(err=>{})
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		changeItem(e) {
			this.nowIndex = e;
		},
		getData() {
			this.$store.dispatch("getBank", {}).then(res => {
				this.cx_card = res.cx_card
				if(res.xy_card){
					this.xy_card = res.xy_card.map(el=>{
						return Object.assign({},el,{flag: false})
					})
				}
			}).catch(err => {});
		},
		//解绑业务
		unbind(type,id){
			var that = this
			uni.showModal({
			    title: '提示',
			    content: '是否确认解绑该通道？',
			    success: function (res) {
			        if (res.confirm) {
			            that.$store.dispatch('unbindCard',{
			            	creditid: id,
			            	channel_type: type,
			            	token: uni.getStorageSync("token")
			            }).then(res=>{
			            	uni.showToast({
			            		title: res.msg,
			            		icon: 'none', //loading   none  success
			            		success: (res)=>{
			            			setTimeout(function(){
			            				that.getData()
			            			},1500)
			            		}
			            	});
			            }).catch(err=>{
							that.getData()
						})
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		changeIcon(index) {
			this.xy_card[index].flag = !this.xy_card[index].flag
		},
		link(type) {
			let url = type == 0 ? '/pages/mine/bank-card/add-credit-card' : '/pages/mine/bank-card/change-bank'
			uni.navigateTo({
				url: url
			})
		}
	},
	onShow() {
		this.getData();
	}
};
